<template>
  <div>
    <el-card shadow="hover">
      <div slot="header">
        <span>反馈/建议</span>
      </div>
      <el-form ref="ruleForm" v-model="form">
        <el-form-item prop="advice">
          <el-input
            type="textarea"
            :rows="5"
            placeholder="请给点建议帮助我改进吧!"
            v-model="form.feedback"
            maxlength="500"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="onSubmit"
            :disabled="!form.feedback"
          >
            提 交
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { feedback } from "../../../api/feedback";

export default {
  data() {
    return {
      form: {
        feedback: ""
      }
    };
  },
  methods: {
    onSubmit() {
      feedback(this.$store.getters.token, {
        advice: this.form.feedback
      }).then(() => {
        this.form.feedback = "";
        this.$notify({
          title: "系统通知",
          message: "提交成功，感谢你的反馈，这将有助于提供更好的产品！",
          type: "success"
        });
      });
    }
  }
};
</script>
